import { useState, useEffect, useCallback } from 'react';
import { ref, push, onChildAdded, off } from 'firebase/database';
import { db } from '../firebase';

export function useSocket(username, activeChat, onReceiveMessage, onReceiveMessageHistory) {
  const [isConnected, setIsConnected] = useState(true);
  const [typingUsers, setTypingUsers] = useState({});

  useEffect(() => {
    const chatRef = ref(db, `chats/${activeChat.id}/messages`);
    
    const onNewMessage = onChildAdded(chatRef, (snapshot) => {
      const message = snapshot.val();
      onReceiveMessage(message);
    });

    return () => {
      off(chatRef, 'child_added', onNewMessage);
    };
  }, [activeChat.id, onReceiveMessage]);

  const sendMessage = useCallback((message, recipient, priority = 'normal') => {
    const chatRef = ref(db, `chats/${activeChat.id}/messages`);
    const newMessage = {
      message: message.trim(),
      username,
      recipient,
      timestamp: Date.now(),
      priority
    };
    return push(chatRef, newMessage);
  }, [username, activeChat.id]);

  // Implement other methods (emitTyping, markMessageAsRead) similarly

  return {
    isConnected,
    typingUsers,
    sendMessage,
    // other methods
  };
}

