import React, { useState, useCallback, useMemo } from 'react';
import { useCollectionData } from 'react-firebase-hooks/firestore';
import { collection, query, orderBy, limit } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import { db, storage } from '../firebase';
import styled from 'styled-components';
import { useSnackbar } from 'notistack';
import ChatHeader from './ChatHeader';
import MessageList from './MessageList';
import MessageInput from './MessageInput';
import TypingIndicator from './TypingIndicator';
import SearchBar from './SearchBar';
import EmojiPicker from './EmojiPicker';
import LoadingSpinner from './LoadingSpinner';
import { useSocket } from '../hooks/useSocket';
import { useAuth } from '../contexts/AuthContext';
import { theme } from '../assets/styles/theme';

const ChatContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: ${theme.colors.background};
  color: ${theme.colors.text};
`;

const ChatContent = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

const MessageArea = styled.div`
  flex: 1;
  overflow-y: auto;
  padding: ${theme.spacing.md};
`;

const InputArea = styled.div`
  display: flex;
  align-items: center;
  padding: ${theme.spacing.md};
  background-color: ${theme.colors.secondary};
  border-top: 1px solid ${theme.colors.border};
`;

const ErrorMessage = styled.div`
  color: ${theme.colors.error};
  text-align: center;
  padding: ${theme.spacing.md};
`;

function Chat({ activeChat }) {
  const { currentUser } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);

  const messagesRef = collection(db, `chats/${activeChat.id}/messages`);
  const messagesQuery = query(messagesRef, orderBy('timestamp'), limit(100));
  const [messages, loading, error] = useCollectionData(messagesQuery, { idField: 'id' });

  const { sendMessage, isConnected, typingUsers, emitTyping } = useSocket(currentUser.uid, activeChat);

  const handleSendMessage = useCallback(async (text, file) => {
    try {
      let fileUrl = null;
      if (file) {
        const fileRef = ref(storage, `chat-files/${activeChat.id}/${Date.now()}-${file.name}`);
        await uploadBytes(fileRef, file);
        fileUrl = await getDownloadURL(fileRef);
      }

      await sendMessage({
        text,
        fileUrl,
        sender: currentUser.uid,
        timestamp: Date.now(),
      });

      if (file) {
        enqueueSnackbar('File uploaded successfully', { variant: 'success' });
      }
    } catch (error) {
      console.error('Error sending message:', error);
      enqueueSnackbar('Failed to send message', { variant: 'error' });
    }
  }, [activeChat.id, currentUser.uid, enqueueSnackbar, sendMessage]);

  const handleReaction = useCallback(async (messageId, reaction) => {
    try {
      await sendMessage({
        type: 'reaction',
        messageId,
        reaction,
        sender: currentUser.uid,
        timestamp: Date.now(),
      });
    } catch (error) {
      console.error('Error sending reaction:', error);
      enqueueSnackbar('Failed to send reaction', { variant: 'error' });
    }
  }, [currentUser.uid, enqueueSnackbar, sendMessage]);

  const memoizedMessageList = useMemo(() => (
    <MessageList
      messages={messages}
      currentUser={currentUser}
      onReaction={handleReaction}
    />
  ), [messages, currentUser, handleReaction]);

  if (loading) {
    return <LoadingSpinner />;
  }

  if (error) {
    return <ErrorMessage>Error: {error.message}</ErrorMessage>;
  }

  return (
    <ChatContainer>
      <ChatHeader chatName={activeChat.name} isConnected={isConnected} />
      <ChatContent>
        <SearchBar />
        <MessageArea>
          {memoizedMessageList}
        </MessageArea>
        <TypingIndicator typingUsers={typingUsers} />
        <InputArea>
          <MessageInput
            onSendMessage={handleSendMessage}
            onTyping={emitTyping}
            isConnected={isConnected}
          />
          <EmojiPicker
            onEmojiSelect={(emoji) => handleSendMessage(emoji.native)}
            showPicker={showEmojiPicker}
            setShowPicker={setShowEmojiPicker}
          />
        </InputArea>
      </ChatContent>
    </ChatContainer>
  );
}

export default Chat;

