import React from 'react';
import styled from 'styled-components';
import { format } from 'date-fns';
import { File } from 'lucide-react';
import { theme } from '../assets/styles/theme';

const MessageContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: ${props => props.$isSent ? 'flex-end' : 'flex-start'};
  margin-bottom: ${theme.spacing.md};
`;

const MessageBubble = styled.div`
  background-color: ${props => props.$isSent ? theme.colors.primary : theme.colors.secondary};
  color: ${props => props.$isSent ? theme.colors.white : theme.colors.text};
  border-radius: ${theme.borderRadius.large};
  padding: ${theme.spacing.sm} ${theme.spacing.md};
  max-width: 70%;
  word-wrap: break-word;
`;

const Timestamp = styled.span`
  font-size: ${theme.fontSizes.small};
  color: ${theme.colors.lightText};
  margin-top: ${theme.spacing.xs};
`;

const FileAttachment = styled.div`
  display: flex;
  align-items: center;
  background-color: ${theme.colors.secondary};
  border-radius: ${theme.borderRadius.medium};
  padding: ${theme.spacing.sm};
  margin-top: ${theme.spacing.sm};
`;

const FileName = styled.span`
  margin-left: ${theme.spacing.sm};
  font-size: ${theme.fontSizes.small};
`;

const ReactionContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: ${theme.spacing.xs};
`;

const Reaction = styled.span`
  background-color: ${theme.colors.secondary};
  border-radius: ${theme.borderRadius.full};
  padding: ${theme.spacing.xs} ${theme.spacing.sm};
  margin-right: ${theme.spacing.xs};
  margin-bottom: ${theme.spacing.xs};
  font-size: ${theme.fontSizes.small};
  cursor: pointer;
`;

function MessageList({ messages, currentUser, onReaction }) {
  return (
    <div>
      {messages && messages.map((msg) => (
        <MessageContainer key={msg.id} $isSent={msg.sender === currentUser.uid}>
          <MessageBubble $isSent={msg.sender === currentUser.uid}>
            {msg.text}
            {msg.fileUrl && (
              <FileAttachment>
                <File size={16} />
                <FileName>{msg.fileName || 'Attachment'}</FileName>
              </FileAttachment>
            )}
          </MessageBubble>
          <Timestamp>{format(msg.timestamp, 'HH:mm')}</Timestamp>
          <ReactionContainer>
            {msg.reactions && Object.entries(msg.reactions).map(([reaction, users]) => (
              <Reaction key={reaction} onClick={() => onReaction(msg.id, reaction)}>
                {reaction} {users.length}
              </Reaction>
            ))}
          </ReactionContainer>
        </MessageContainer>
      ))}
    </div>
  );
}

export default MessageList;

